.popup {
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba($color-black, 0.8);
  z-index: 3000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;

  // quite laggy performance
  // backdrop-filter: invert();
  // -webkit-backdrop-filter: invert();

  // @supports (-webkit-backdrop-filter: blur(10px)) or
  //   (backdrop-filter: blur(10px)) {
  //   backdrop-filter: blur(10px);
  //   -webkit-backdrop-filter: blur(10px);
  // }

  &__content {
    @include absCenter;
    width: 75%;
    background-color: $color-white;
    box-shadow: 0 2rem 4rem rgba($color-black, 0.2);
    border-radius: 3px;
    display: table;
    overflow: hidden;
    opacity: 0; //for move in popup effect
    transform: translate(-50%, -50%) scale(0.25); //for move in popup effect
    transition: all 0.5s 0.2s;
  }

  &__left {
    width: 33.33333%;
    display: table-cell;
  }

  &__right {
    width: 66.66666%;
    display: table-cell;
    vertical-align: middle;
    padding: 3rem 5rem;
  }

  &__img {
    display: block;
    width: 100%;
  }

  &__text {
    font-size: 1.4rem;
    margin-bottom: 4rem;

    //column layout
    column-count: 2;
    column-gap: 4rem; //1em = 14px
    column-rule: 1px solid $color-grey-light-2;
    hyphens: auto;
  }

  //Open state
  // when we click at something or url changes for that anker elements
  //opden popup
  &:target {
    opacity: 1;
    visibility: visible;
  }

  &:target &__content {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
  // close popup
  &__close {
    &:link,
    &:visited {
      color: $color-grey-dark;
      position: absolute;
      top: 2.5rem;
      right: 2.5rem;
      font-size: 3rem;
      text-decoration: none;
      display: inline-block;
      transition: all 0.2s;
      line-height: 1;
    }

    &:hover {
      color: $color-primary;
    }
  }
}
